import React, { useEffect } from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import reportWebVitals from './reportWebVitals';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import store from './store/index';
import { SnackbarProvider } from 'notistack';

// Redux

import { Provider } from 'react-redux';
import { useSelector, useDispatch } from 'react-redux';
import { setToken, setUser, selectUser } from './store/userAuth';
import api from './services/index';

// Route & Pages

import DefaultLayout from './layouts/DefaultLayout';
import App from './App';
import Home from './pages/Home';
import HomeDetail from './pages/HomeDetail';
import Login from './pages/Loggin';
import Error from './pages/Error';
import {
	createBrowserRouter,
	RouterProvider,
	Route,
	BrowserRouter,
	Navigate,
	Outlet,
	useNavigate,
} from 'react-router-dom';

// helpers
import { getCookie } from './helpers/customizeCookie';

const ProtectedRoute = ({ redirectPath = '/login', children }) => {
	const authUser = useSelector(selectUser);
	const currentToken = getCookie('token', true);
	const dispatch = useDispatch();
	const navigate = useNavigate();

	const getUser = async token => {
		const res = await api.user.getUserInfo(token);

		if (!res) {
			console.log('ng nhp tht bi. Lin h IT  c h tr. Code 01');
			return <Navigate to={redirectPath} replace />;
		}

		try {
			if (res.status && res.status > 199 && res.status < 400) {
				dispatch(setUser(res.data.data.user));
				return children ? children : <Outlet />;
			} else {
				dispatch(setUser(null));
				const msg = 'ng nhp tht bi: ' + (res.data.message || String(res)) + ' Code 02';
				console.log(msg);
				navigate(redirectPath);
				return;
			}
		} catch (error) {
			const msg = 'ng nhp tht bi: ' + String(error) + ' Code 03';
			console.log(msg);
			return <Navigate to={redirectPath} replace />;
		}
	};
	if (authUser.user) {
		// logged
		return children ? children : <Outlet />;
	} else {
		if (currentToken) {
			getUser('currentToken');
		} else {
			return <Navigate to={redirectPath} replace />;
		}
	}
};

const router = createBrowserRouter([
	{
		path: '/',
		element: <App />,
		errorElement: <Error />,
	},
	{
		path: '/login',
		element: <Login />,
		errorElement: <Error />,
	},
	{
		path: '/home',
		element: (
			<ProtectedRoute>
				<Home />
			</ProtectedRoute>
		),
		errorElement: <Error />,
		children: [
			{
				path: '/home/:homeId',
				element: (
					<ProtectedRoute>
						<HomeDetail />
					</ProtectedRoute>
				),
			},
		],
	},
]);
const root = ReactDOM.createRoot(document.getElementById('root'));
const theme = createTheme({
	palette: {
		primary: {
			main: '#1976D2',

			white: '#ffffff',
			black: '#000',
		},
		secondary: {
			main: '#1976D2',
		},
		text: {
			primary: '#1976D2',
			white: '#ffffff',
			black: '#000',
		},
		semantic: {
			error: '#D11313',
			success: '#3AC430',
			info: '#0569FF',
			warning: '#FE9705',
		},
	},
});
root.render(
	<Provider store={store}>
		<ThemeProvider theme={theme}>
			<SnackbarProvider maxSnack={3} autoHideDuration={4000}>
				<App />
			</SnackbarProvider>
		</ThemeProvider>
	</Provider>
);

reportWebVitals();
